* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: grid;
  place-content: center;
  font-family: "Roboto Mono", sans-serif;
  background-color: #dbfff4;
}

.card {
  width: 95vw;
  max-width: 600px;
  box-shadow: 0px 0px 44px rgba(148, 146, 120, 0.63);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}
.card span,
.card label,
.card button {
  text-transform: capitalize;
}

.card__top article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px 0;
  font-size: 1.15rem;
}
.card__top article:first-child {
  border-bottom: 3px solid #dbfff4;
  margin-bottom: 15px;
}
.card__top article .card__tip-amount,
.card__top article .card__total-amount {
  position: relative;
  font-size: 3.5rem;
  font-weight: 700;
}
.card__top article .card__tip-amount::before,
.card__top article .card__total-amount::before {
  position: absolute;
  content: "$";
  top: -5%;
  left: -1ch;
  font-size: 3rem;
}

.card__input {
  position: relative;
  display: flex;
  padding: 40px 0;
  background-color: #f1fbff;
}
.card__input::before {
  position: absolute;
  content: "";
  top: 0;
  left: 60%;
  width: 3px;
  height: 100%;
  background-color: #d5f3ff;
}
.card__input div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}
.card__input div:first-child {
  position: relative;
  width: 60%;
}
.card__input div:first-child::before {
  position: absolute;
  content: "";
  left: 12%;
  width: 20px;
  aspect-ratio: 9/16;
  color: teal;
  background-image: url("../images/dollar.svg");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  z-index: 2;
}
@media screen and (min-width: 600px) {
  .card__input div:first-child::before {
    top: 10%;
    width: 25px;
  }
}
.card__input div:last-child {
  width: 40%;
  position: relative;
}
.card__input div:last-child::before {
  position: absolute;
  content: "";
  left: 12%;
  width: 25px;
  aspect-ratio: 9/16;
  background-image: url("../images/people.svg");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  z-index: 2;
}
@media screen and (min-width: 600px) {
  .card__input div:last-child::before {
    width: 35px;
  }
}
.card__input input {
  position: relative;
  border: 0;
  border-bottom: 3px dotted #cccccc;
  padding-left: 20%;
  width: 80%;
  font-size: 2rem;
  font-family: "Roboto Mono", sans-serif;
  font-family: "Inter", sans-serif;
  background-color: #f1fbff;
}
.card__input input:focus, .card__input input:focus-visible, .card__input input:active {
  outline: 2px solid #dbfff4;
  outline: 2px solid #acffe6;
  border-radius: 10px;
}
.card__input input#billAmount {
  padding-left: 17%;
}
@media screen and (min-width: 600px) {
  .card__input input#billAmount {
    padding-left: 20%;
  }
}
.card__input input#numberOfPeople {
  padding-left: 33%;
}
@media screen and (min-width: 600px) {
  .card__input input {
    font-size: 2.75rem;
  }
}
.card__input label {
  text-align: center;
}

.card__tip-amounts {
  background-color: #d5f3ff;
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
}
.card__tip-button {
  width: 18%;
  aspect-ratio: 16/7;
  padding: 10px 0;
  background-color: #fff;
  color: #0092cc;
  font-family: "Roboto Mono", sans-serif;
  font-size: 1.25rem;
  border-radius: 10px;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.2s ease-in;
}
.card__tip-button:hover, .card__tip-button.active {
  background-color: #0092cc;
  color: #fff;
}
.card__tip-button:focus, .card__tip-button:focus-visible, .card__tip-button:active {
  border: 2px solid #0092cc;
  border-radius: 10px;
  outline: none;
}

.card__bottom {
  display: flex;
  justify-content: space-between;
  padding: 15px 20px;
}
.card__bottom .calc {
  margin-left: auto;
  font-size: 1.25rem;
  padding: 0.5em 1.5em;
  border-radius: 10px;
  border: 2px solid transparent;
  background-color: #ff853f;
  color: white;
  font-family: "Roboto Mono", sans-serif;
  cursor: pointer;
  transition: all 0.2s ease-in;
}
@media screen and (min-width: 600px) {
  .card__bottom .calc {
    padding: 0.5em 3em;
  }
}
.card__bottom .calc:hover, .card__bottom .calc.active {
  background-color: white;
  color: #ff853f;
  border: 2px solid #ff853f;
}
.card__bottom .calc:focus, .card__bottom .calc:focus-visible, .card__bottom .calc:active {
  border: 2px solid #ff853f;
  border-radius: 10px;
  outline: none;
}